<template>
  <div id="work-page">
    
    <section class="section black work-visual" data-theme="white">
      <ContentVisual :dimmed="true" mode="visualTrigger">
        <template #title>
          <ContentTitle
            :titleXL = "[ { delay: 0, text: `Finding the right one is` }, { delay: 1, text: `what we do best.` } ]"
            :visualTitle = "[ { delay: 2, text: `What We Made` } ]"
            theme="black"
            align="center"
          ></ContentTitle>
        </template>
        <template #visual>
          <video preload="auto" loop autoplay muted playsinline><source :src="video" type="video/mp4"></video>
        </template>
      </ContentVisual>
    </section>

    <div id="content">
      <WorkList :listGroups="listGroups"></WorkList>
    </div>

  </div>
</template>

<script>
import FetchPageMixin from '@/mixins/FetchPageMixin'
// '@/mixins/fetchPageMixin';

// component
import ContentVisual from '@/components/common/ContentVisual.vue'
import ContentTitle from '@/components/common/ContentTitle.vue'
import WorkList from '@/components/common/WorkList.vue'

// assets
import video from '@/assets/images/work/video.mp4'

export default {
  mixins: [FetchPageMixin],
  components: {
    ContentVisual,
    ContentTitle,
    WorkList,
  },
  data() {
    return {
      video,
      listNum: 1,
      listState: this.$store.getWorksStatus,
      listGroups: [],
      data: [],
    }
  },
  methods: {
    async fetchLists() {
      // if ( !this.listState ) return false;
      try {
        await this.$store.dispatch('FETCH_GET_WORK_LIST', { num: this.listNum });
        this.listnum++;
        this.setDataLayout();
        // console.log('try', this.listNum);
      } catch (e) {
        console.log('error', e);
      }
    },
    setDataLayout() {
      const dataLayout = [
        { num: 5, layout: 51 },
        { num: 4, layout: 43 },
        { num: 2, layout: 21 },
        { num: 3, layout: 31 },
        { num: 2, layout: 21 },
        { num: 2, layout: 21 },
        { num: 3, layout: 31 },
        { num: 4, layout: 41 },
        { num: 4, layout: 46 },
        { num: 3, layout: 34 },
        { num: 3, layout: 31 },
        { num: 4, layout: 44 },
        { num: 3, layout: 34 },
        { num: 3, layout: 33 },
        { num: 4, layout: 41 },
        { num: 4, layout: 43 },
        { num: 2, layout: 21 }
      ];
      let dataGroup = [...this.$store.getters.getWorks];
      let list = dataLayout.map( a => { return { layout: a.layout, data: dataGroup.splice(0, a.num) } });
      this.listGroups = list;
    }
  },
  async mounted() {
    await this.fetchLists();
    this.PageReady();
  }
}
</script>

<style scoped>
#work-page { background: #191919; }
</style>